<template>
  <div>
    <div class="query-item-form">
      <div class="query-item">
        <div class="label">:</div>
        <div class="items">
          <el-input v-model="query.name" clearable placeholder=""></el-input>
        </div>
      </div>
      <div class="query-item">
        <div class="label">:</div>
        <div class="items">
          <el-input v-model="query.customerNo" clearable placeholder=""></el-input>
        </div>
      </div>
      <div class="query-item">
        <div class="label">:</div>
        <div class="items">
          <el-select v-model="query.channelId" clearable filterable placeholder="" style="width: 100%">
             <el-option :value="item.id" :label="item.name" :key="item.id" v-for="item in allChannel"/>
          </el-select>
        </div>
      </div>
      <div class="query-item">
        <div class="label">:</div>
        <div class="items">
          <el-input v-model="query.username" clearable placeholder=""></el-input>
        </div>
      </div>
      <div class="buttons">
        <el-button  size="small" type="primary" @click="page.pageNo=1;queryData()"><i class="fa fa-search"/></el-button>
        <el-button  size="small" type="success" @click="toAdd()"><i class="fa fa-plus"/></el-button>
      </div>
    </div>
    <common-table :columns="columns" :data="tableData" :pager="page" @handleSizeChange="handleSizeChange"
                 @handleCurrentChange="handleCurrentChange">
      <el-table-column slot="table_operation" align="center" fixed="right" label="" width="220" :resizable="false">
        <template slot-scope="scope">
          <el-button size="small" type="text" @click="toShow(scope.row)"></el-button>
          <el-button size="small" type="text" @click="toEdit(scope.row)"></el-button>
          <el-button size="small" type="text" @click="deleteRow(scope.row)"></el-button>
          <el-button size="small" type="text" @click="recharge(scope.row)"></el-button>
        </template>
      </el-table-column>
    </common-table>
      <!---->
      <add   @event="onEvent" :all-channels="allChannel" :is-visible="addShow"/>
      <edit :is-visible="editShow" :all-channels="allChannel"  @event="onEvent" :current-row="currentRow"/>
      <show :current-row="currentRow"  :is-visible="openShow" @event="onEvent"/>
      <recharge :customer="currentRow"  :is-visible="rechargeShow" @event="onEvent"/>
  </div>
</template>

<script>
  import add from "./add";
  import edit from "./edit";
  import show from "./show";
  import {queryChannelPager} from "@/api/channelapi";
  import {queryCustomerPager} from "@/api/customerapi";
  import recharge from "./recharge";
  export default {
    name:'channels',
    components:{
      'add':add,
      'edit':edit,
      'show':show,
      'recharge':recharge
    },

    data() {
      return {
        query:{},
        columns: [
          {prop: 'name', width:'160', label: '',fixed:"left", align: 'center'},
          {prop: 'customerNo', width:'160', label: '', align: 'center'},
          {prop: 'username', width:'160', label: '', align: 'center'},
          {prop: 'channelDesc',label: '', align: 'center'},
          {prop: 'balance', width:'160', label: '', align: 'center',formatter(row, column, cellValue){
              return $price(cellValue)
            }},
          {prop: 'notification', label: '', width:"200", align: 'center',formatter(row, column, cellValue){
              return $price(cellValue)
            }},
          {prop: 'sales',label: '',width:"200", align: 'center',formatter(row, column, cellValue){
            return $price(cellValue)
            }},
          {prop: 'status', label: '',width:"80",  align: 'center',formatter(row, column, cellValue){
              if(cellValue == 0){
                return '';
              }else if(cellValue==1){
                return '';
              }
            }},
          {prop: 'contact',label: '', align: 'center'},
          {prop: 'phone',width:'120', label: '', align: 'center'},
          {prop: 'createdAt', label: '', width:'190',align: 'center',formatter(row, column, cellValue){
            return new $day(cellValue).format("YYYY-MM-DD HH:mm:ss")
            }
          },
        ],
        addShow:false,
        openShow:false,
        editShow:false,
        rechargeShow:false,
        tableData: [],
        allChannel:[],
        page: {
          pageNo: 1,
          pageSize: 10,
          total: 0
        },
        currentRow:{},
      }
    },
    mounted() {
      this.queryData();
      this.queryChannel();
    },
    methods: {
      queryChannel(){
        queryChannelPager({"pageNo":1,"pageSize":999}).then(resp=>{
          this.allChannel = resp.data.data
        })
      },
      recharge(row){
        this.currentRow = row
        this.rechargeShow = true
      },
      toAdd(){
        this.addShow = true
      },
      toShow(row){
        let _this = this;
        _this.currentRow = row
        _this.openShow = true
      },
      toEdit(row){
        let _this = this;
        _this.currentRow = row;
        _this.editShow = true
      },
      deleteRow(row) {
        let _this = this;
        _this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          deleteChannel(row).then(resp=>{
            if(resp.code == 0){
              _this.$message.success("");
              _this.queryData();
            }else{
              _this.$message.error(resp.message);
            }
          })
        });
      },
      queryData(){
        let _this = this;
        queryCustomerPager(this.$nullParam(Object.assign(_this.query,_this.page))).then(resp=>{
          if(resp.code == 0){
            _this.tableData = resp.data.data
            _this.page.total = resp.data.totalRows
          }else{
            _this.$message.error("");
          }
        })
      },
      onEvent(e){
        let that = this;
        if(e.cmd == 'edit'){
          that.editShow = true
          that.openShow = false
        }else if(e.cmd == 'delete'){
          that.openShow = false
          that.queryData()
        }else if(e.cmd == 'closeAdd'){
          that.addShow = false
        }else if(e.cmd == 'closeShow'){
          that.openShow = false
        }else if(e.cmd == 'closeEdit'){
          that.editShow = false
        }else if(e.cmd == 'addSuccess'){
          that.addShow = false
          that.queryData()
        }else if(e.cmd == 'editSuccess'){
          that.editShow = false
          that.queryData()
        }else if(e.cmd == 'closeRecharge'){
          that.rechargeShow = false
        }else if(e.cmd == 'rechargeSuccess'){
          that.rechargeShow = false
          that.queryData()
        }
      },
      // 
      handleSizeChange(val) {
         this.page.pageSize = val
         this.queryData();
      },
      // 
      handleCurrentChange(val) {
        this.page.pageNo = val
        this.queryData();
      },
    },
  }
</script>
<style scoped type="text/css">
</style>



