<template>
  <el-drawer
    title=""
    :visible.sync="isShow"
    size="50%"
    @close="close"
    @opened="open"
    :wrapperClosable="false"
    destroy-on-close
  >
      <el-form ref="addChannel" :rules="rules" :model="model" label-width="150px">
        <el-form-item label="" prop="name">
          <el-input v-model="model.name" maxlength="64" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="customerNo">
          <el-input v-model="model.customerNo" maxlength="32" @blur="$set(model,'customerNo',$event.target.value)" onkeyup="value=value.replace(/[^\w_.]/g,'')" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="username">
          <el-input v-model="model.username" maxlength="32" @blur="$set(model,'username',$event.target.value)" onkeyup="value=value.replace(/[^\w_.]/g,'')" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="model.password" maxlength="32" @blur="$set(model,'password',$event.target.value)" onkeyup="value=value.replace(/[^\w_.]/g,'')" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="channelId">
          <el-select v-model="model.channelId" placeholder="" style="width: 100%">
            <el-option :value="item.id" :label="item.name" :key="item.id" v-for="item in allChannels"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="sales">
          <el-input v-model="model.sales" maxlength="128" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="notification">
          <el-input v-model="model.notification" maxlength="128" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="contact">
          <el-input v-model="model.contact" maxlength="16" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="model.phone" @blur="model.phone=$event.target.value" onkeyup="value=value.replace(/[^\d]/g,'');value = value.slice(0,11)" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="remark">
          <el-input v-model="model.remark" maxlength="64" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="banner">
          <upload :type="'image'" :limit="5" :multiple="true" @upload="upload" v-if="reload" :file-list="model.files"></upload>
        </el-form-item>
      </el-form>

    <div class="drawer-footer">
      <el-button type="primary"  size="small" @click="submitForm('addChannel')"><i class="fa fa-check"/></el-button>
    </div>
  </el-drawer>
</template>

<script>
  import {check,addCustomer} from "@/api/customerapi";
  import upload from "@/components/common/upload"
  export default {
    name:'tenantAdd',
    props:{
      isVisible:false,
      allChannels:[],
    },

    components:{
      'upload':upload
    },
    watch:{
      isVisible(value){
        this.isShow = value
      }
    },
    data() {
      let validateCode = (i,j,callback)=>{
        if(this.model.customerNo && this.model.customerNo.length>0){
          let params = {
            key:'customerNo',
            val:this.model.customerNo
          }
          check(params).then(resp=>{
            if(resp.data){
              callback()
            }else{
              callback(new Error(""));
            }
          })
        }else{
          callback(new Error(""));
        }
      }
      let validateUsername = (i,j,callback)=>{
        if(this.model.username && this.model.username.length>0){
          let params = {
            key:'username',
            val:this.model.username
          }
          check(params).then(resp=>{
            if(resp.data){
              callback()
            }else{
              callback(new Error(""));
            }
          })
        }else{
          callback(new Error(""));
        }
      }
      return {
        isShow:false,
        model:{
          name:'',
          customerNo:'',
          username:'',
          channelId:'',
          password:'',
          notification:'',
          sales:'',
          contact:'',
          phone:'',
          files:[],
        },

        reload:true,
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ],
          customerNo: [
            { required: true,validator:validateCode,  trigger: 'blur' }
          ],
          username: [
            { required: true,validator:validateUsername,  trigger: 'blur' }
          ],
          password: [
            { required: true,message: '',  trigger: 'blur' }
          ],
          channelId: [
            { required: true,message: '',  trigger: 'blur' }
          ],
          notification: [
            { required: true, message: ',', trigger: 'blur',pattern:/(^[\d]|^[1-9][\d]*)($|[\.][\d]{0,3}$)/ }
          ],
          sales: [
            { required: true, message: ',', trigger: 'blur',pattern:/(^[\d]|^[1-9][\d]*)($|[\.][\d]{0,3}$)/ }
          ],
          contact:[
            { required: true, message: '', trigger: 'blur'}
          ],
          phone:[
            { required: true, message: '', trigger: 'blur',pattern : /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/ }
          ]
        },
      };
    },
    mounted() {
    },
    methods: {
      open(){
      },
      upload(data){
        this.model.files = data.fileList
      },
      close(){
        this.model = {
          name:'',
          customerNo:'',
          username:'',
          channelId:'',
          password:'',
          notification:'',
          sales:'',
          contact:'',
          phone:'',
          files:[],
        }
        this.$emit("event",{cmd:"closeAdd"})
      },
      submitForm(formName) {
        let _this  = this
        if(this.model.files && this.model.files.length>0){
          let fileList = [];
          _this.model.files.forEach(e=>{
            fileList.push(e.fileId)
          })
          _this.model.fileList = fileList
        }
        this.$refs[formName].validate((valid) => {
          if (valid) {
            addCustomer(_this.model).then(resp=>{
                if(resp.code == 0){
                  _this.$message.success("");
                  _this.$emit("event",{cmd:"addSuccess"});
                }else{
                  _this.$message.error(resp.message);
                }
            })
          } else {
            return false;
          }
        });
      },
    }
  }
</script>
<style type="text/css" scoped>
</style>



